@import url('https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@700&family=Dancing+Script&family=Poppins:ital,wght@1,700&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Days+One&display=swap');


.navbarbox{
    border-radius:7px;
    width: 95%;
   left:25px;
    /* height:45px; */
   display: flex;
   align-items: center;
    margin: auto;
    z-index: 1;
    
  
    position: fixed;
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(10px);
  
    border:1px solid rgba(255, 255, 255, 0.18);
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
}
.menu{
  text-align: center;
  border-radius: 15px;
}
.menu>a{
  color:blue;
  text-decoration: none;
  text-align: center;
}
.menuItem{
  width: 98%;
  margin: auto;
  text-decoration: none;
background-color: black;
 border:2px solid #5ddcff;
  height: 35px;
  text-align: center;
  display: flex;
  align-items: center;
  text-decoration: none;
  font-size: 25px;
  text-align: center;
  text-decoration: none;
  align-content: center;
  padding: 15px 5px;
  margin-top: 15px;
  border-radius: 10px;
}
.menuItem>p{
  margin-left: 70px;
  padding: 5px 0;
  font-size:30px ;
  margin-top: 15px;
  color: #5ddcff;
  font-weight: bold;
}

.css-1l1kldz{
 height:400px;
}
.css-1l1kldz{
 background-color: transparent;
 border:3px solid #03bcf4; 
 height: 50px;
 text-align: center;
}
.menubutton{
  display: none;
 
  /* visibility: hidden; */
 }
.active {
    text-decoration: none;
    color: #ff8f00;
    /* background-color: #039ba1; */
  }
  .default{
    text-decoration: none;
    color: white;
  }
  .navlink{
    width: 60%;
display: flex;
align-items: center;
justify-content: space-around;
  }
  .link{
    transition: 0.2s;
    color: white;
    text-decoration: none;
    width: fit-content;
    font-family: 'Days One', sans-serif;
  }

  .link:hover{
    text-shadow: 0 0 10px #ba68c8,
    0 0 20px #ba68c8,
    0 0 20px #ba68c8,
    0 0 20px #ba68c8,
    0 0 20px #ba68c8;

    transform: scale(1.1);
  }

  .logo{
    font-size: 30px;
    font-family: 'Cinzel Decorative', cursive;
    /* font-family: 'Dancing Script', cursive; */
    /* font-family: 'Poppins', sans-serif; */
    color: white;
    margin-left: 30px;
  animation :falsh 2s linear infinite;
  }
  @keyframes falsh {
    0%{
           text-shadow: 0 0  40px #03bcf4,
           0 0  40px  #03bcf4,
            0 0  40px #03bcf4,
            0 0  40px #03bcf4

    }
    50%{
      text-shadow: 0 0  10px #03bcf4,
      0 0  5px #03bcf4,
       0 0  5px #03bcf4,
       0 0  5px #03bcf4
    }
    100%{
      text-shadow: 0 0  40px #03bcf4,
      0 0  40px  #03bcf4,
       0 0  40px #03bcf4,
       0 0  40px #03bcf4
    }
    
  }

  
  .cv{
  height:3rem;
  width: auto;
  border-width: 4px;
  border-radius: 15px;
  text-align: center;
  background-color: transparent;

  color:#5ddcff;
  border-color:#5ddcff;
  transition: 0.7s;
  padding: 0.5rem 0.5rem;
}
.cv{
  color:white;
  text-shadow: 0 0 10px #5ddcff,
  0 0 20px #5ddcff,
  0 0 20px #5ddcff,
  0 0 20px #5ddcff,
  0 0 20px #5ddcff;
  box-shadow: 0 0 10px #5ddcff,
  0 0 20px #5ddcff,
  0 0 20px #5ddcff,
  0 0 20px #5ddcff,
  0 0 20px #5ddcff,
}







  @media only screen and (max-width: 1200px) {
    .cv{
      width:125px;
      }
      .link{
        font-size: 15px;
      }

  }

  @media only screen and (max-width: 600px) {
    .navbarbox{
      left:0;
    }

    .menubutton{
   display: contents;
   height: 95%;
   width: 2.5rem;
   border: 1px solid red;
     
    
     }
     
     .active{
      display: none;
     }
     .menu{
  
      border: 1px solid rgba(135, 255, 241, 0.3);
     }
     .menuItem{
      background: linear-gradient(275deg, rgba(135, 255, 241, 0.3), rgba(255, 255, 255, 0));
      backdrop-filter: blur(20px);
      /* -webkit-backdrop-filter: blur(5px); */
  
      border: 1px solid rgba(135, 255, 241, 0.3);
     }
    
     .menuItem:hover{
      text-decoration: none;
      
      box-shadow: 0 0  10px #03bcf4,
      0 0  5px #03bcf4,
       0 0  5px #03bcf4,
       0 0  5px #03bcf4
    
  }
  

.cv{
width:125px;
}
  .logo{
    font-size: 20px;
  }
}




