@property --rotate {
    syntax: "<angle>";
    initial-value: 160deg;
    inherits: false;
  }
  @import url('https://fonts.googleapis.com/css2?family=Marck+Script&family=Orbitron&display=swap');
  @import url('https://fonts.googleapis.com/css2?family=Days+One&display=swap');


  .fakebox{
    width: 100px ;
    height: 3rem;
    /* border: 1px solid green; */
  }

  .projectvideo{
    position: absolute;
    z-index: -1;
    top:200vh;
    left: 0;
    width: 100%;
    height: fit-content;
      object-fit: cover;
 }

.projectsbox{
    width: 95%;
    height: fit-content;
    margin: auto;
    margin-top: 40px;
    /* border: 1px solid red; */
    
   text-align: center;
   
}
.projectsgridbox{
    width: 90%;
    margin: auto;
  
   
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap:2rem 2rem;
    padding:1rem 1rem
}
.projectsbox>h1{
  font-size: 40px;
  color: white;
  font-family:'Marck Script', cursive;
  animation :aboute 2s linear infinite;
  font-family: 'Days One', sans-serif;

 
}
@keyframes aboute {
  0%{
         text-shadow: 0 0  40px #5ddcff,
         0 0  40px  #5ddcff,
          0 0  40px #5ddcff,
          0 0  40px #5ddcff

  }
  50%{
    text-shadow: 0 0  10px #5ddcff,
    0 0  5px #5ddcff,
     0 0  5px #5ddcff,
     0 0  5px #5ddcff
  }
  100%{
    text-shadow: 0 0  40px #5ddcff,
    0 0  40px  #5ddcff,
     0 0  40px #5ddcff,
     0 0  40px #5ddcff
  }
  
}

.projectcard{
    content: "";
    /* padding: 0.1rem 0.1rem; */
  height:40rem;
 
    border-radius: 8px;
/*     
    background-image: linear-gradient(
      var(--rotate)
      , #5ddcff,transparent  43%,transparent );
     
      animation: spinback 2.5s linear infinite; */
      /* background: url(/public/Shape-block.png); */
      background: linear-gradient(275deg, rgba(135, 255, 241, 0.3), rgba(255, 255, 255, 0));
      backdrop-filter: blur(20px);
      /* -webkit-backdrop-filter: blur(5px); */
  
      border: 1px solid rgba(135, 255, 241, 0.3);
      padding:10px 10px;
      height: 800px;
     


}
@keyframes spinback {
    0% {
      --rotate: 0deg;
    }
    100% {
      --rotate: 360deg;
    }
  }

.pdetails{
    width: 99%;
    height:98%;
    margin: auto;
    margin-top: 0.6%;
    display:flex;
    flex-direction: column;
 
    /* background-image:linear-gradient(black,#0a0221); */
}

/*******links*********/
.slides{
  margin: auto;
  width: 95%;
  height:50%;

}
.details{
  width: 95%;
  margin: auto;
  height: 50%;
  /* border:1px solid #5ddcff; */
  border: 2px solid rgba(135, 255, 241, 0.3);

  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding:0.2rem 0.5rem;
  /* background-image:linear-gradient(to right bottom ,black  30%,#0a0221  ); */
  
  ;
}
.title{
  width: 90%;

  color: white;
  font-size: 1.5rem;
  text-align: left;
  /* font-weight: bold; */
  justify-content: center;
  font-family: 'Days One', sans-serif;
}

.tool{
  width: 90%;

  color: white;
  font-size: 1rem;
  margin-top: 0.1rem;
  text-align: left;
  font-family: 'Days One', sans-serif;
 
  justify-content: center;
}
.features{
  width: 90%;
  height:fit-content;
  /* color: #5ddcff; */
  color:white;
  font-size: 0.9rem;
  margin-top: 0.4rem;
  text-align: left;
  line-height:15px;
  margin-top: 1rem;
  font-family: 'Days One', sans-serif;
 
}
.features>h6{
  margin-top: 1rem;
  font-family: 'Days One', sans-serif;
}
/* .features>div{
  display: grid;
  height: fit-content;
  grid-template-columns: repeat(2,1fr);
  justify-content: center;
} */
.features>div>p{
  margin-top: 3px;
}
.links{
  width: 90%;
  height:10% ;
  margin-top: 1rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.links>a>button{
  background-color: transparent;
  border: 2px solid #5ddcff;
  color: #5ddcff;
  border-radius: 7px ;
  padding:0 10px;
  font-family: 'Days One', sans-serif;
}
.links>a>button:hover{
 box-shadow: 0 0 5px #5ddcff,
 0 0 5px #5ddcff,
 0 0 5px #5ddcff,
 0 0 5px #5ddcff,
 0 0 5px #5ddcff;
 text-shadow:0 0 5px #5ddcff,
 0 0 20px #5ddcff,
 0 0 20px #5ddcff,
 0 0 20px #5ddcff,
 0 0 20px #5ddcff; ;
}
.slides>div{
 width: 99%;
 height:95%;
 overflow: hidden;
}
.slides>div>img{
  width: 100%;
}








